import React from 'react';

const width = window.screen.width; //
const height = window.screen.height; //
export default class NewsDetail extends React.Component {

    constructor(props) {
        super(props);
        this.backClick = this.backClick.bind(this);

    }

    backClick(){
        this.props.location.params.myRoute.goBack();
    }
    // componentWillMount(){
    //     window.open("http://www.baidu.com");
    // }    
    render() {
        return (<div style={{ flex: 1 }}>
            <div style={{
                width: width,
                height: 64,
                backgroundColor: "#21212b",
                display: "flex",
                justifyContent: "space-between"
            }}>
                <img src={require('../image/back.png')} 
                onClick={this.backClick}
                style={{ width: 20, height: 20, paddingTop: 34, paddingLeft: 15, }} />

            </div>
            <iframe id="if" src={this.props.location.params.url}
                style={{
                    width: width,
                    height: height - 64,
                    borderWidth:0,
                }}
            ></iframe>
        </div>);
    }
}

