import React from 'react'
import NEWSNEWS from "./news_news"
import NewsAnnouncement from "./NewsAnnouncement"
import NewsResearch from "./NewsResearch"

const width = window.screen.width; //
const height = window.screen.height; //
var datas = ["News", "Announcement", "Videos", "Research"];

export default class NEWS extends React.Component {
    constructor(props) {
        super(props);
        this.leftClick = this.leftClick.bind(this);
        this.topClick = this.topClick.bind(this);
        this.selectpage = this.selectpage.bind(this);


        this.state = {
            selectIndex: 0
        }

    }


    leftClick() {
        // 路由跳转
        // this.props.myRoute.push({
        //     pathname: "/newsdetail",
        //     params: { url: "http://www.baidu.com" }
        // });
    }

    topClick(index) {
        this.setState({
            selectIndex: index
        })
    }

    selectpage() {
        switch (this.state.selectIndex) {
            case 0:
                {
                    return <NEWSNEWS myRoute={this.props.myRoute} />;
                }
                break;
            case 1:
                {
                    return <NewsAnnouncement myRoute={this.props.myRoute} />;
                }
                break;
            case 2:
                {
                    return <NEWSNEWS myRoute={this.props.myRoute} />;

                }
                break;
            case 3:
                {
                    return <NewsResearch myRoute={this.props.myRoute} />;

                }
                break;

            default:
                break;
        }
    }


    render() {

        const style4 = {
            fontSize: 15,
            color: "#FFFFFF"
        }
        const styleLine = {
            height: 2,
            backgroundColor: '#387CFE',
            position: "absolute",
            top: 40,
            left: 5,
            right: 5,
            borderRadius: 2
        }



        return (
            <div style={style1}>
                <div style={topViewStyle}>
                    <img src={require('../image/menu.png')} style={menuStyle} onClick={this.leftClick} />
                    <p style={homeTitleStyle}>News</p>
                    <img src={require('../image/Message.png')} style={messageStyle} />
                </div>

                <div style={style2}>
                    <div style={style3}>
                        {datas.map((title, index) =>
                            <div style={{ display: "inline-block", padding: 10, position: "relative" }}
                                onClick={() => this.topClick(index)}
                            >
                                <label style={style4}>{title}{index}</label>
                                <div style={{
                                    height: 2,
                                    backgroundColor: (this.state.selectIndex == index) ? "#387CFE" : "#21212b",
                                    position: "absolute",
                                    top: 40,
                                    left: 5,
                                    right: 5,
                                    borderRadius: 2
                                }}
                                >
                                </div>
                            </div>

                        )}
                    </div>




                </div>
                {this.selectpage()}
            </div>
        )
    }
}

var style1 = {
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    bottom: 49,
    width: width,
    height: height - 49,
    backgroundColor: "#21212b",
    textAlign: "center",
    overflow: "scroll",
}
var topViewStyle = {
    // position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    height: 64,
    backgroundColor: '#21212B',
    // flexDirection: "row",
    // justifyContent: "space-around",
}

var menuStyle = {
    position: "absolute",
    left: 15,
    top: 34,
    width: 24,
    height: 24,
}
var homeTitleStyle = {
    height: 64,
    paddingTop: 5,
    color: "#ffffff",
    fontSize: 25,
    textAlign: "cneter",
}


var messageStyle = {
    position: "absolute",
    right: 15,
    top: 34,
    width: 24,
    height: 24,
}

var style2 = {
    width: width,
    height: 64,
    backgroundColor: "#21212b",
    textAlign: "center",
    overflow: "scroll",
}
var style3 = {
    width: width,
    height: 64,
    backgroundColor: "#21212b",
    textAlign: "center",
    overflow: "scroll",
}


